
import SmallModal from "@/components/SmallModal.vue"
import Signup from "@/components/login/Signup.vue"
import { ref, defineComponent } from 'vue'
import { useRouter} from 'vue-router'
import { useStore } from 'vuex'
import $ from 'jquery'

export default defineComponent({
    setup() {
        //router
        const router = useRouter()
        //vuex
        const store = useStore();
        //modal
        const display_modal = ref(0);
        //data
        const stu_id = ref("");
        const password = ref("");
        function submitAll(){
            if(stu_id.value.length>0 && password.value.length>0){
                $.post('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/login/auth.php',{
                    account: stu_id.value,
                    password: password.value
                },function(data){
                    var r = JSON.parse(data);
                    switch(r.status){
                        case 'welcome':
                            localStorage.nsysua_token = r.token;
                            router.push('/main/home');
                            break;
                        case 'wrong_password':
                            alert("密碼錯誤 Wrong password");
                            break;
                        case 'account_is_not_found':
                            alert("找不到此帳號 Account is not found");
                            break;
                        default:
                            alert("請聯絡田徑隊");
                            console.log(r.status);
                    }
                });
            }else{
                alert("請填寫正確資料");
            }
        }
        return {
            display_modal,
            stu_id,
            password,
            submitAll,
        }
    },
    components: {
        SmallModal,
        Signup,
    }
})
