
import { defineComponent, ref , watch } from 'vue'
import axios from 'axios'
import { useRouter} from 'vue-router'
import { useStore } from 'vuex'
import $ from 'jquery'

export default defineComponent({
    setup() {
        //router
        const router = useRouter();
        //vuex
        const store = useStore();
        //language
        const lang_code:any = ref(store.state.lang_code);
        watch(lang_code, ()=>{
            store.commit('changeLang',lang_code.value);
        });
        //data
        const stu_id:any = ref("");
        const name:any = ref("");
        const dept:any = ref("");
        const g_year:any = ref("");
        const sex:any = ref("");
        const email:any = ref("");
        const password:any = ref("");
        const confirmation:any = ref("");
        //registered
        const is_existing: any = ref(false);
        function isExisting(){
            $.post('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/login/exist.php',{account: stu_id.value},function(data: any){
                if(data==1){
                    is_existing.value = true;
                }else{
                    is_existing.value = false;
                }
            });
        }
        //student ID format
        const stu_id_format:any = ref(true);
        function stuId(){
            if(/[A-Z][0-9]{9}/.test(stu_id.value)){
                stu_id_format.value = true;
            }else{
                stu_id_format.value = false;
            }
        }
        //confirmation
        const is_match:any = ref(true);
        function isMatch(){
            if(password.value == confirmation.value && password.value.length>0 && confirmation.value.length>0){
                is_match.value = true;
            }else{
                is_match.value = false;
            }
        }
        const has_pass:any = ref(false);
        function hasPass(){
            if(password.value.length>0){
                has_pass.value = true;
            }else{
                has_pass.value = false;
            }
        }
        //dept
        const dept_list:any = ref({});
        axios.get('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/dept.php').then((res:any)=>{
            console.log(res.data);
            dept_list.value = res.data;
        });
        //emil format
        const email_format:any = ref(true);
        function emailFormat(){
            if(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(email.value)){
                email_format.value = true;
            }else{
                email_format.value = false;
            }
        }
        //submit data
        function submitAll(){
            if(stu_id.value.length>0 && name.value.length>0 && email.value.length>0 && dept.value.length>0 && g_year.value.length>0 && sex.value.length>0 && has_pass.value==true && is_match.value==true && is_existing.value==false && stu_id_format.value==true && email_format.value==true){
                $.post('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/login/signup.php',{
                    stu_id: stu_id.value,
                    name: name.value,
                    dept: dept.value,
                    g_year: g_year.value,
                    sex: sex.value,
                    email: email.value,
                    password: password.value
                },function(data:any){
                    if(data!="done"){
                        alert("請聯絡田徑隊");
                        console.log(data);
                    }else{
                        alert("完成 Done");
                        router.push("/login");
                    }
                });
            }else{
                alert("請確認所有資料都填寫。 Please fill in all fields.");
            }
        }
        return {
            lang_code,
            dept_list,
            stu_id,
            name,
            dept,
            g_year,
            sex,
            email,
            password,
            confirmation,
            isMatch,
            is_match,
            hasPass,
            has_pass,
            submitAll,
            isExisting,
            is_existing,
            stuId,
            stu_id_format,
            emailFormat,
            email_format,
        }
    },
})
